import logo from './logo.svg';
import './App.css';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom'
import { useEffect, useState } from 'react';
import DBstore from './components/Movies/DBstore'
import CrewMember from './components/Movies/CrewMember';

function App() {

  const [ids, setIds] = useState([])

  useEffect(() => {
    DBstore.getCrewMembers();
    DBstore.emitter.addListener('GET_MEMBERS_SUCCESS', () => {
      setIds(DBstore.data)
    })
  }, [])

  return (
    <>
      <h1>Title</h1>
      <a href = "/3">3</a>
      
        <Routes>
          {ids.map((crewMember) => {
            let id = crewMember.CrewMemberID
            return <Route path={"/" + id} element={<CrewMember key={crewMember.CrewMemberID} member={crewMember} />} />
          })}
        </Routes>
    </>
  );

}

export default App;
