import { useState } from 'react'

function CrewMember(props) {
    const { member } = props;
    const [crewMemberID, setCrewMemberID] = useState(member.CrewMemberID);
    const [movieID, setMovieID] = useState(member.MovieID)
    const [name, setName] = useState(member.Name);
    const [role, setRole] = useState(member.Role);

    return (<>
        <h1>Crew member</h1>
        <p>ID: {crewMemberID}</p>
        <p>MovieID: {movieID}</p>
        <p>Name: {name}</p>
        <p>role: {role}</p>        
    </>)
}

export default CrewMember;